:root {
  --background-color: #ffffcc;
  --line-color: #333333;
  background-color: var(--line-color);
}

.App {
  text-align: center;
}

.game {
  display: flex;
  flex-direction: column; 
  position: absolute; 
  margin-top: 5%;
  margin-left: auto;
  margin-right: auto;
  left: 0;
  right: 0;
  text-align: center;
  width: 448px;
  border: 5px solid var(--background-color);
  background-color: var(--background-color);
  border-radius: 4%;
}

.game-info {
  display: grid;
  grid-template-rows: 50% 50%;
  grid-template-areas: 
    "s1"
    "s2";
  border-top: 6px solid var(--line-color);
}

.piece-container{
  grid-area: s1;
  white-space: nowrap;
}

.status{
  grid-area: s2;
  font-size: 18px;
  display: flex;
  align-items: center; /* Vertical center alignment */
  justify-content: center; /* Horizontal center alignment */
}

.piece-container-blue {
  display: inline-block;
}

.piece-container-red {
  display: inline-block;
}

ul.blue {
  text-align:justify;
}
ul.red {
  text-align:justify;
}
ul:after {
  content: '';
  display: inline-block;
}
ul:before {
  content: '';
  display: block;
}

li {
  display:inline-block;
  margin-right: -1em;
}

.board {
  background-image: url('./images/Board.png');
  background-repeat: no-repeat;
  background-position: center;
  background-size: 442px;
}

.board-row:after {
  clear: both;
  content: "";
  display: table;
}

.square {
  border: 0px;
  float: left;
  font-size: 24px;
  font-weight: bold;
  line-height: 64px;
  height: 64px;
  text-align: center;
  width: 64px;
  background:rgba(0, 0, 0, 0);
}

.piece {
  border:4px solid black;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  position: absolute;
  transition: all 1s ease-out 0s;
  z-index: 2;
}

.piece.red {
  background: #920707;
  top:448px;
  left:448px;
}

.piece.blue {
  background: #000879;
  top:0px;
  left:0px;
}

.piece.highlight {
  border:4px solid greenyellow;
}

.circle.highlight{
  border:6px solid greenyellow;
  border-radius: 50%;
  height: 16px;
  width: 16px;
  position: absolute;
  transition: all 1s ease-in-out 0s;
  z-index: 1;
}

.piece-panel-red {
  border:4px solid black;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  z-index: 2;
  background: #920707;
}

.piece-panel-blue{
  border:4px solid black;
  border-radius: 50%;
  height: 24px;
  width: 24px;
  z-index: 2;
  background: #000879;
}